


















import { Component, Prop, Vue, Emit } from 'vue-property-decorator';
// @ts-ignore
import dragAndDrop from '@/helpers/dragAndDrop.js';

@Component({
  name: 'WidgetSettings',
})
export default class WidgetSettings extends Vue {
  mounted() {
    dragAndDrop('js-location-list', 'js-location-item');
  }

  private showSettings = true;
  private inputValue = '';

  private get locationsArray(): Object {
    return this.$store.getters['weather/locationsData'];
  }

  private addNewLocation(): void {
    this.$store.commit('weather/pushNewLocation', this.inputValue);
    this.inputValue = '';
  }

  private removeLocation(location: String): void {
    this.$store.commit('weather/removeLocation', location);
    this.update();
  }

  @Emit()
  private update(): void {}

  @Emit()
  private hideSettings(): void {
    const values: String[] = [];
    (this.$refs.locationList as any)
      .querySelectorAll('.js-location-item')
      .forEach((item: any) => {
        values.push(item.textContent);
      });

    this.$store.commit('weather/updateLocations', values);
    this.update();

    this.showSettings = false;
  }
}
